import React from 'react';
import PropTypes from 'prop-types';
import "./Movie";

function Movie({id, title, year, summary, poster, rating, genres}) {
	return <div className="movies_movie">
		<img src={poster} alt={title} />
		<div className="movie_data">
			<h3 className="movie_title">{title}</h3>
			<h5 className="movie_year">{year}</h5>
			<ul className="genres">
				{genres.map((genre, idx) => <li key={idx} className="genres_genre">#{genre}</li> )}
			</ul>
			<p className="movie_summary">{summary.slice(0, 180)}...</p>
		</div>
	</div>
}

Movie.prototype = {
	id: PropTypes.number.isRequired,
	year: PropTypes.number.isRequired,
	rating: PropTypes.number.isRequired,
	title: PropTypes.string.isRequired,
	summary: PropTypes.string.isRequired,
	poster: PropTypes.string.isRequired,
	genres: PropTypes.arrayOf(PropTypes.stirng).isRequired
};

export default Movie;